.study-areas-container {
  background-color: $grey;
  background: linear-gradient(#000000, $grey, #303030);
  position: relative;
  padding-top: 15vw;

  h1 {
    margin: 0 auto 2vw auto;
    text-align: center;
    color: $white;
    font-size: 5vw;
    span {
      color: $pink;
    }
  }

  .content-container {
    box-shadow: 1vw 1vw 2vw rgba(0, 0, 0, 0.75);
  }

  .image {
    margin: 0 auto;
    background-color: $pink;
    height: 60%;
    width: 80%;

    .slick-slider {
      position: static;
      .slick-dots {
        z-index: 2;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        margin: 0;
        width: 60%;

        li {
          margin: 0px 1vw;

          button:before {
            border: solid 0.2vw #000;
          }
        }

        .slick-active button:before {
          background-color: #000;
          border: none;
          height: 75%;
          width: 75%;
        }
      }
    }
  }

  .caption {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: $white;
    width: 70%;
    padding: 4vw 6vw 0 6vw;
    z-index: 1;

    h2 {
      font-size: 3.5vw;
      text-align: center;
      margin-bottom: 2vw;
      color: #000;
    }

    p {
      height: 10vw;
      font-family: 'Montserrat Ultra Light';
      margin-bottom: 2vw;
      font-size: 2.2vw;
      overflow: scroll;
    }

    .button-container {
      width: 100%;
      text-align: center;

      button.button {
        box-shadow: 0.5vw 0.5vw 0.6vw rgba(0,0,0,0.3);
        font-size: 2.5vw;
        padding: 1vw 4vw;
        margin-bottom: 12vw;
      }
    }
  }
}



